export const __metadata = { PACKAGE_NAME: '@wix/media' };

export function createCategory(
  httpClient: HttpClient
): CreateCategorySignature {
  return (category: NonNullablePaths<EnterpriseCategory, `displayName`, 2>) =>
    universalCreateCategory(
      category,
      // @ts-ignore
      { httpClient }
    );
}

interface CreateCategorySignature {
  /**
   * Fetch a list of random media from different providers, using site information to customize results when available
   * @param - The category object that will be created
   * @returns A list of items matching the request
   */
  (category: NonNullablePaths<EnterpriseCategory, `displayName`, 2>): Promise<
    NonNullablePaths<EnterpriseCategory, `_id` | `publishStatus`, 2>
  >;
}

export function deleteCategory(
  httpClient: HttpClient
): DeleteCategorySignature {
  return (categoryId: string) =>
    universalDeleteCategory(
      categoryId,
      // @ts-ignore
      { httpClient }
    );
}

interface DeleteCategorySignature {
  /**
   * Delete a category including all its subcategories - but not the items
   * @param - Category id
   */
  (categoryId: string): Promise<void>;
}

export function updateCategory(
  httpClient: HttpClient
): UpdateCategorySignature {
  return (_id: string, category: UpdateCategory) =>
    universalUpdateCategory(
      _id,
      category,
      // @ts-ignore
      { httpClient }
    );
}

interface UpdateCategorySignature {
  /**
   * Update category details
   * @param - Id of the category
   * @returns The updated category
   */
  (_id: string, category: UpdateCategory): Promise<
    NonNullablePaths<EnterpriseCategory, `_id` | `publishStatus`, 2>
  >;
}

export function getCategory(httpClient: HttpClient): GetCategorySignature {
  return (categoryId: string, options?: GetCategoryOptions) =>
    universalGetCategory(
      categoryId,
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface GetCategorySignature {
  /**
   * Get information about a specific category
   * @param - Category id
   * @returns The category details
   */
  (categoryId: string, options?: GetCategoryOptions): Promise<
    NonNullablePaths<
      EnterpriseCategoryTree,
      `category._id` | `category.publishStatus` | `subCategories`,
      3
    >
  >;
}

export function enterpriseOnboarding(
  httpClient: HttpClient
): EnterpriseOnboardingSignature {
  return (accountId: string, options?: EnterpriseOnboardingOptions) =>
    universalEnterpriseOnboarding(
      accountId,
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface EnterpriseOnboardingSignature {
  /**
   * Create the enterprise category under "enterprise-media" main category
   * the caller identity must be have the same accountId of the request
   * @param - The account id of the organization - will be used as the organization category id
   */
  (accountId: string, options?: EnterpriseOnboardingOptions): Promise<
    NonNullablePaths<
      EnterpriseOnboardingResponse,
      `category._id` | `category.publishStatus`,
      3
    >
  >;
}

export function getMediaManagerCategories(
  httpClient: HttpClient
): GetMediaManagerCategoriesSignature {
  return () =>
    universalGetMediaManagerCategories(
      // @ts-ignore
      { httpClient }
    );
}

interface GetMediaManagerCategoriesSignature {
  /**
   * Get the account category tree details
   */
  (): Promise<
    NonNullablePaths<
      GetMediaManagerCategoriesResponse,
      | `category.category._id`
      | `category.category.publishStatus`
      | `category.subCategories`,
      4
    >
  >;
}

export const onEnterpriseCategoryCreated = EventDefinition(
  'wix.media.enterprise_public_media.v1.enterprise_category_created',
  true,
  (event: EnterpriseCategoryCreatedEnvelope) =>
    renameKeysFromRESTResponseToSDKResponse(
      transformPaths(event, [
        {
          transformFn: transformRESTFloatToSDKFloat,
          paths: [{ path: 'entity.sortOrder' }],
        },
        {
          transformFn: transformRESTTimestampToSDKTimestamp,
          paths: [
            { path: 'entity.createdDate' },
            { path: 'entity.updatedDate' },
            { path: 'metadata.eventTime' },
          ],
        },
      ])
    )
)<EnterpriseCategoryCreatedEnvelope>();
